/* ðŸŒ„ Background & Body */
body, html {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background: url('https://images.unsplash.com/photo-1504674900247-0877df9cc836?auto=format&fit=crop&w=1470&q=80') no-repeat center center fixed;
  background-size: cover;
  color: #fff;
}

/* ðŸŒŸ App Container */
.app {
  position: relative;
  min-height: 100vh;
  padding: 40px 20px;
}

.overlay {
  position: absolute;
  inset: 0;
  background: rgba(0,0,0,0.6);
  z-index: 0;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 1200px;
  margin: auto;
  text-align: center;
}

/* âœï¸ Input Section */
.input-section {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-bottom: 30px;
}

.input-section input[type="text"],
.input-section select,
.input-section input[type="file"] {
  padding: 10px;
  border-radius: 8px;
  border: none;
  min-width: 180px;
  font-size: 16px;
}

.input-section button {
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  background-color: #ff7f50;
  color: white;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
}

.input-section button:hover {
  background-color: #ff4500;
  transform: translateY(-2px);
}

/* ðŸ“‚ Tabs */
.tabs {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.tabs button {
  background: rgba(255,255,255,0.2);
  border: none;
  padding: 10px 20px;
  color: #fff;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.tabs button.active {
  background: #ff7f50;
}

/* ðŸ² Recipe Grid & Cards */
.result-grid {
  display: grid;
  gap: 20px;
  margin-top: 20px;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;

  /* Dynamic scaling */
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

/* Single card wider */
.result-grid > .recipe-card:only-child {
  max-width: 400px;
  margin: 0 auto;
}

/* Recipe Card */
.recipe-card {
  background: rgba(255,255,255,0.95);
  color: #333;
  padding: 20px;
  border-radius: 15px;
  text-align: left;
  box-shadow: 0 6px 15px rgba(0,0,0,0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.recipe-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 25px rgba(0,0,0,0.2);
}

.recipe-card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 10px;
}

.recipe-card h4 {
  margin-top: 0;
  color: #ff7f50;
}

.recipe-card p {
  margin: 6px 0;
  font-size: 14px;
}

/* Buttons side by side - single line */
.card-buttons {
  display: flex;
  gap: 12px;
  margin-top: 12px;
  flex-wrap: nowrap; /* force single line */
}

.card-buttons button {
  flex: 1;
  padding: 10px 12px;
  border-radius: 8px;
  border: none;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap; /* prevent wrapping */
  min-height: 40px;
}

.card-buttons button:not(.remove) {
  background: linear-gradient(135deg, #ff7f50, #ff4500);
  color: #fff;
}

.card-buttons button:not(.remove):hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}

.card-buttons button.remove {
  background: linear-gradient(135deg, #f44336, #d32f2f);
  color: #fff;
}

.card-buttons button.remove:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0,0,0,0.2);
}

/* Badges */
.badges {
  display: flex;
  gap: 8px;
  margin: 8px 0;
  flex-wrap: wrap;
}

.badge {
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  color: white;
}

.badge.vegetarian { background: #4caf50; }
.badge.non-vegetarian { background: #ff5722; }
.badge.vegan { background: #009688; }
.badge.easy { background: #8bc34a; }
.badge.medium { background: #ffc107; color: #333; }
.badge.hard { background: #f44336; }

/* Match Percentage */
.badge.match-percentage {
  background-color: #ff9800;
  color: white;
  padding: 3px 6px;
  border-radius: 12px;
  font-size: 0.75rem;
  margin-left: 5px;
}

/* Missing ingredients */
.missing-ingredients {
  color: #e53935;
  font-size: 0.8rem;
  margin: 2px 0;
}

/* View Page */
.view-page {
  position: relative;
  min-height: 100vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 50px 20px;
  animation: fadeIn 0.6s ease;
}

.view-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
}

.view-content {
  position: relative;
  z-index: 2;
  max-width: 1200px;
  background: rgba(255, 255, 255, 0.05);
  padding: 30px;
  border-radius: 12px;
  backdrop-filter: blur(8px);
}

.view-flex {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
}

.view-left { flex: 1; min-width: 280px; }
.view-right { flex: 1; min-width: 280px; display: flex; justify-content: center; align-items: center; }
.view-right img { width: 100%; border-radius: 12px; object-fit: cover; max-height: 500px; }

.view-left h2 { color: #ff7f50; }
.view-left h3 { margin-top: 20px; color: #ffebcd; }

.serving-buttons {
  display: flex; align-items: center; gap: 10px; margin-bottom: 12px;
}

.serving-buttons button {
  padding: 6px 10px;
  border-radius: 6px;
  border: none;
  background-color: #ff7f50;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
}

.serving-buttons button:hover { background-color: #ff4500; }

.serving-buttons span {
  font-size: 18px; font-weight: 600; min-width: 24px; text-align: center;
}

/* Rating */
.rating-stars span {
  cursor: pointer;
  font-size: 22px;
  margin-right: 5px;
}

.star-active { color: #FFD700; }
.star-inactive { color: #ccc; }

/* Back Button */
.back-btn {
  display: block;
  background-color: #ff7f50;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 20px 0 0;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.back-btn:hover { background-color: #ff4500; transform: scale(1.05); }

/* ðŸ”¹ Responsive */
@media(max-width: 768px) {
  .input-section { flex-direction: column; align-items: center; }
  .result-grid { grid-template-columns: 1fr; }
  .view-flex { flex-direction: column; }
  .view-right { justify-content: center; }
  .card-buttons { flex-direction: column; }
  .card-buttons button { width: 100%; }
}

/* Fade In Animation */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}
/* ðŸ”¹ Autocomplete Dropdown */
.autocomplete-wrapper {
  position: relative;
  width: 200px;
  margin: 0 auto;
}

.autocomplete-suggestions {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #fff;
  color: #333;
  border-radius: 6px;
  max-height: 180px;
  overflow-y: auto;
  box-shadow: 0 4px 8px rgba(0,0,0,0.15);
  z-index: 10;
}

.suggestion {
  padding: 8px 10px;
  cursor: pointer;
  font-size: 14px;
}

.suggestion:hover {
  background-color: #ff7f50;
  color: #fff;
}
